<template>
    <div class="role">

        <div class="role__users">
            <span class="text-black ml-1">Foydalanuvchilar</span>
                            
            <v-select class="mt-4" :items="usersList" item-text="name" item-value="id"
                v-model="user" label="Foydalanuvchini tanlang" outlined>
            </v-select>
        </div>


        <Sidebar></Sidebar>
        <Navbar></Navbar>

        <OmborxonaIp></OmborxonaIp>
        <OmborxonaMato></OmborxonaMato>
        <OmborxonaYoqa></OmborxonaYoqa>

        <OutsourceMatoToquv></OutsourceMatoToquv>
        <OutsourceYoqaToquv></OutsourceYoqaToquv>
        <OutsourceBoyoqxona></OutsourceBoyoqxona>


        <SupplyReadyMato></SupplyReadyMato>
        <SupplyReadyYoqa></SupplyReadyYoqa>
        <SupplyAcessory></SupplyAcessory>
        <SupplyTailoring></SupplyTailoring>


        <Factory></Factory>
        <ReadyProducts></ReadyProducts>
    </div>
</template>

<script>
import {mapState, mapMutations, mapActions} from "vuex";
import Sidebar from './Sidebar.vue'
import Navbar from './Navbar.vue'

import OmborxonaIp from './OmborxonaIp.vue'
import OmborxonaMato from './OmborxonaMato.vue'
import OmborxonaYoqa from './OmborxonaYoqa.vue'

import OutsourceMatoToquv from './OutsourceMatoToquv.vue'
import OutsourceYoqaToquv from './OutsourceYoqaToquv.vue'
import OutsourceBoyoqxona from './OutsourceBoyoqxona.vue'

import SupplyReadyMato from './SupplyReadyMato.vue'
import SupplyReadyYoqa from './SupplyReadyYoqa.vue'
import SupplyAcessory from './SupplyAcessory.vue'
import SupplyTailoring from './SupplyTailoring.vue'

import Factory from './Factory.vue'
import ReadyProducts from './ReadyProducts.vue'

export default {
    components:{
        Sidebar,
        Navbar,

        OmborxonaIp,
        OmborxonaMato,
        OmborxonaYoqa,

        OutsourceMatoToquv,
        OutsourceYoqaToquv,
        OutsourceBoyoqxona,

        SupplyReadyMato,
        SupplyReadyYoqa,
        SupplyAcessory,
        SupplyTailoring,

        Factory,
        ReadyProducts
    },
    data(){
        return{
            user: '',
            usersList: [],
        }
    },
    watch: {
        
    },
    async mounted(){
        await this.refresh();
    },
    computed:{
        ...mapState(
            {
                roles: state => state.main.roles
                // income_ips: state => state.warehouse.income_ips
            }
        ),
    },
    methods:{
        ...mapActions(
            {
                fetchRoles: 'main/fetchRoles',
            }
        ),
        async refresh() {
            await this.fetchRoles();
            this.usersList = this.roles;
            this.usersList.splice(0,1);
        },
     
    }
}
</script>

